body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: darkgray;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{position:fixed;width:100%;background-color:#2b2b2b;display:flex;justify-content:space-around;align-items:center;padding:10px 0}header .header-logo{height:50px;overflow:hidden;max-width:120px}header .header-logo img{width:100%;height:100%;object-fit:cover}.header-options{display:flex;flex-direction:row;grid-gap:40px;gap:40px}.header-options .temp-switch{display:flex;flex-direction:row;align-items:center;grid-gap:15px;gap:15px;color:#fff9ee}.header-options .temp-switch label{position:relative;margin:5px 0;cursor:pointer}.header-options .temp-switch label input{-webkit-appearance:none;-moz-appearance:none;appearance:none;display:none}.header-options .temp-switch label input:checked ~ .indicator{left:40px}.header-options .temp-switch label input:checked ~ .indicator::before{background:#0f0;box-shadow:0 0 2px #0f0, 0 0 5px #0f0, 0 0 10px #0f0, 0 0 15px #0f0, 0 0 20px #0f0, 0 0 25px #0f0, 0 0 30px #0f0}.header-options .temp-switch label span{position:relative;display:block;width:80px;height:40px;background-color:#222;border-radius:40px;box-shadow:inset 0 2px 15px rgba(0,0,0,0.2),inset 0 2px 2px rgba(0,0,0,0.2),inset 0 -1px 1px rgba(0,0,0,0.2)}.header-options .temp-switch label .indicator{position:absolute;top:0;left:0;width:40px;height:40px;background:linear-gradient(to bottom, #444, #222);border-radius:50%;box-shadow:0 2px 5px rgba(0,0,0,0.5),inset 0 1px 1px rgba(255,255,255,0.1);transition:0.5s;transform:scale(0.9)}.header-options .temp-switch label .indicator::before{content:'';position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:5px;height:5px;transition:0.5s;background:#f00;border-radius:50%;box-shadow:0 0 2px #f00, 0 0 5px #f00, 0 0 10px #f00, 0 0 15px #f00, 0 0 20px #f00, 0 0 25px #f00, 0 0 30px #f00}.header-options .lang-switch{display:flex;flex-direction:row;align-items:center;grid-gap:10px;gap:10px}.header-options .lang-switch select{background:#424b4d;color:#fff;padding:5px;border:none}@media (max-width: 600px){header{padding:10px 0}header .header-logo{height:40px}.header-options{transform:scale(0.8)}}

.loading{height:100vh;width:100vw;margin:auto;display:grid;place-content:center}.loading svg{transform:rotateY(180deg) rotateZ(0deg);-webkit-animation:rotation 1s infinite;animation:rotation 1s infinite}@-webkit-keyframes rotation{100%{transform:rotateY(180deg) rotateZ(-360deg)}}@keyframes rotation{100%{transform:rotateY(180deg) rotateZ(-360deg)}}.current-container{width:100%;height:auto;text-align:center;color:white;margin-top:80px}.current-container .current-body{display:flex;flex-flow:row wrap;justify-content:space-around}.current-container .current-body .today{width:600px}.current-container .current-body .today .current-description{font-size:larger}.current-container .current-body .today .current-more-info{display:flex;flex-direction:row;justify-content:space-around;margin:20px 0}.current-container .current-body .forecast{width:400px;display:flex;flex-direction:column;font-size:12px;padding:0 10px;font-size:16px}.current-container .current-body .forecast ul{list-style:none;padding:0;margin-top:25px}.current-container .current-body .forecast ul li{display:flex;flex-direction:row;justify-content:space-between;align-items:center;border-bottom:2px solid white;margin-bottom:30px;padding-bottom:4px}.current-container .current-body .forecast ul li .day{font-weight:bold;padding-bottom:-2px}.current-container .current-body .forecast ul li .temps{display:flex;flex-direction:row;justify-content:flex-end;align-items:center;grid-gap:5px;gap:5px}@media (max-width: 600px){.current-container{height:auto}}

.card{margin:10px}.card .card-front{width:250px;height:300px;min-width:250px;position:relative;display:flex;flex-direction:column;background:linear-gradient(to bottom, #840314, #f20);box-shadow:0 10px 15px rgba(0,0,0,0.8);border-end-end-radius:30%;border-start-start-radius:20px;color:white}.card .card-front .card-top{display:flex;flex-direction:row;justify-content:space-around;margin-top:10px}.card .card-front .card-top input{border-radius:40px;text-align:center}.card .card-front .card-top button{width:20px;height:20px;border-radius:50%;text-align:center;padding:0;box-shadow:0 0 5px black, 0 0 10px black;cursor:pointer}.card .card-front .card-top .button-close{color:white;background-color:#840314}.card .card-front .card-top .button-info{color:white;font-style:italic;background-color:#274df7;font-weight:400}.card .card-front .card-current{display:flex;flex-direction:column;width:100%;height:100px;justify-content:center;align-items:center}.card .card-front .card-current span{height:50px;width:50%}.card .card-front .card-current img{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.card .card-front .card-current .card-description{width:50%;display:flex;flex-direction:column}.card .card-front .card-current .card-description h3{margin-bottom:0}.card .card-front .card-more-info{display:flex;flex-direction:row;justify-content:space-around;margin-top:20px}.card .card-front .card-more-info div{display:flex;flex-direction:row;align-items:center;grid-gap:10px;gap:10px}.card .card-back{display:none}.card .card-front.active{display:none}.card .card-back.active{display:flex;flex-direction:column;width:250px;height:300px;min-width:250px;background:linear-gradient(to bottom, #0c0384, #0059ff);box-shadow:0 10px 15px rgba(0,0,0,0.8);border-top-right-radius:20px;border-bottom-left-radius:30%;color:white}.card .card-back.active .card-top{display:flex;flex-direction:row;justify-content:flex-end;margin:10px 10px 0 0}.card .card-back.active .card-top button{width:20px;height:20px;border-radius:50%;text-align:center;padding:0;box-shadow:0 0 5px black, 0 0 10px black;cursor:pointer}.card .card-back.active .card-top .button-info{color:white;font-style:italic;background-color:#274df7}.card .card-back.active .card-body{display:flex;flex-direction:column;width:90%;font-size:12px;padding:0 10px}.card .card-back.active .card-body ul{list-style:none;padding:0;margin-top:25px}.card .card-back.active .card-body ul li{display:flex;flex-direction:row;justify-content:space-between;align-items:center;border-bottom:2px solid white;margin-bottom:20px;padding-bottom:4px}.card .card-back.active .card-body ul li .day{font-weight:bold;padding-bottom:-2px}.card .card-back.active .card-body ul li .temps{display:flex;flex-direction:row;justify-content:flex-end;align-items:center;grid-gap:5px;gap:5px}.card:nth-child(1){margin-left:60px}

.cities-body{position:relative;display:flex;flex-flow:row;justify-content:space-around;width:100%;overflow-x:auto}.cities-body .cities-weather{display:flex;flex-flow:row;width:100%;height:330px;justify-content:flex-start;overflow-x:auto;transition:1s;margin-top:-10px}.cities-body .cities-add{display:flex;flex-direction:column;align-items:center;margin:10px;background:linear-gradient(to bottom, #840314, #f20);width:250px;height:300px;min-width:250px;box-shadow:0 10px 15px rgba(0,0,0,0.8);border-end-end-radius:30%;border-start-start-radius:20px}.cities-body .cities-add input{margin:100px 0 50px;width:70%;border-radius:40px;text-align:center}.error-black{position:fixed;width:100vw;height:100%;background-color:rgba(0,0,0,0.7);z-index:1000}.error-black .error-show{position:relative;top:50%;left:50%;transform:translate(-50%, -50%);width:50%;min-width:350px;height:100px;padding:20px;display:flex;flex-direction:column;align-items:center;background-color:white;border-radius:50px;box-shadow:0 0 20px black, 0 0 10px black;font-size:20px}.error-black .error-show button{align-self:end;color:white;width:-webkit-max-content;width:-moz-max-content;width:max-content;padding:5px;margin:25px 15px 0 0;background:#444;font-size:16px;border-radius:10px}@media (max-width: 600px){.error-black .error-show{font-size:15px}}

.App{text-align:center;display:flex;flex-direction:column}

