header {
  position: fixed;
  width: 100%;
  background-color: #2b2b2b;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px 0;
  .header-logo {
    height: 50px;
    overflow: hidden;
    max-width: 120px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.header-options {
  display: flex;
  flex-direction: row;
  gap: 40px;
  .temp-switch {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 15px;
    color: rgb(255, 249, 238);
    label {
      position: relative;
      margin: 5px 0;
      cursor: pointer;
      input {
        appearance: none;
        display: none;
      }
      input:checked ~ .indicator {
        left: 40px;
      }
      input:checked ~ .indicator::before {
        background: #0f0;
        box-shadow: 0 0 2px #0f0, 0 0 5px #0f0, 0 0 10px #0f0, 0 0 15px #0f0,
          0 0 20px #0f0, 0 0 25px #0f0, 0 0 30px #0f0;
      }
      span {
        position: relative;
        display: block;
        width: 80px;
        height: 40px;
        background-color: #222;
        border-radius: 40px;
        box-shadow: inset 0 2px 15px rgba(0, 0, 0, 0.2),
          inset 0 2px 2px rgba(0, 0, 0, 0.2),
          inset 0 -1px 1px rgba(0, 0, 0, 0.2);
      }
      .indicator {
        position: absolute;
        top: 0;
        left: 0;
        width: 40px;
        height: 40px;
        background: linear-gradient(to bottom, #444, #222);
        border-radius: 50%;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5),
          inset 0 1px 1px rgba(255, 255, 255, 0.1);
        transition: 0.5s;
        transform: scale(0.9);
      }
      .indicator::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 5px;
        height: 5px;
        transition: 0.5s;
        background: #f00;
        border-radius: 50%;
        box-shadow: 0 0 2px #f00, 0 0 5px #f00, 0 0 10px #f00, 0 0 15px #f00,
          0 0 20px #f00, 0 0 25px #f00, 0 0 30px #f00;
      }
    }
  }
  .lang-switch {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    select {
      background: rgb(66, 75, 77);
      color: #fff;
      padding: 5px;
      border: none;
    }
  }
}

@media (max-width: 600px) {
  header {
    padding: 10px 0;
    .header-logo {
      height: 40px;
    }
  }
  .header-options {
    transform: scale(0.8);
  }
}
